import React from 'react';
import {Link} from 'react-router-dom';


const Home = ()=>{
 return(
  <section className='container'>
   <article>
    <h1>President</h1>
      <figure>
       <img src='data:image/jpeg;base64,' alt='President of Kenya, William Ruto' className='images'/>
      </figure>
      <article className='info'>
       <h2 className='home'>William Samoei Ruto</h2>
      <h4 className='home'>United Democratic Alliance (UDA)</h4>
      <h5 className='home'>(He garnered most votes in Kiambu County as compared to the other presidential contenstants as seen in the <Link to='pres-results'>IEBC Results</Link>) </h5>

      </article>
      
   </article>
  </section>
 )
}

export default Home;